/* Component style rules */
.ttcomponent-dot-loader {
	background-color:rgba(0,0,0, .1);
}

#header .ttcomponent-dot-loader .aDot,
.ttcomponent-dot-loader .aDot {
	background-color: #fff;
	animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.ttcomponent-dot-loader .aDot:nth-child(1) {
	left: 6px;
	animation: ttcomponent-dot-loader1 0.6s infinite;
}
.ttcomponent-dot-loader .aDot:nth-child(2) {
	left: 6px;
	animation: ttcomponent-dot-loader2 0.6s infinite;
}
.ttcomponent-dot-loader .aDot:nth-child(3) {
	left: 26px;
	animation: ttcomponent-dot-loader2 0.6s infinite;
}
.ttcomponent-dot-loader .aDot:nth-child(4) {
	left: 45px;
	animation: ttcomponent-dot-loader3 0.6s infinite;
}
@keyframes ttcomponent-dot-loader1 {
	0% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}
@keyframes ttcomponent-dot-loader3 {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(0);
	}
}
@keyframes ttcomponent-dot-loader2 {
	0% {
		transform: translate(0, 0);
	}
	100% {
		transform: translate(19px, 0);
	}
}