/* Component basic layout rules */

.ttcomponent-homepageBoxes.container { margin-top:-7.5rem; position:relative; z-index:10; overflow:visible;}
.ttcomponent-homepageBoxes.container .wrapwidg { margin:0px; padding:1.5rem;}
.ttcomponent-homepageBoxes.container .widget_padding { display:-webkit-box; display:-ms-flexbox; display:flex; -ms-flex-wrap: wrap; flex-wrap: wrap; -webkit-box-align:center; -ms-flex-align:center; align-items:center; padding:0;}
.ttcomponent-homepageBoxes.container .widget_padding > div {-webkit-box-flex:1;-ms-flex:1;flex:1; max-width:100%; }
.ttcomponent-homepageBoxes.container *{ color:#fff; }



.ttcomponent-homepageBoxes div.wrapwidg:before {
	content:'';
	display:block;
	width:100%;
	height:20px;
	position:absolute;
	top:100%;
	left:0;
	background-image: url(data:image/svg+xml;base64,);
	background-size:cover;
	background-repeat:no-repeat;
	background-position: center bottom;
}

.ttcomponent-homepageBoxes.container > .widget_padding > .column_generico:first-child {
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-wrap:wrap;
		flex-wrap:wrap;
/* 	-webkit-box-align:center; */
/* 		-ms-flex-align:center; */
/* 			align-items:center; */
	-webkit-box-pack:center;
		-ms-flex-pack:center;
		justify-content:center;
}

.ttcomponent-homepageBoxes h2.widget_title > i { display:none;}
.ttcomponent-homepageBoxes.container .box-01 { max-width:100%; }
.ttcomponent-homepageBoxes.container .box-02 { max-width:100%;}
.ttcomponent-homepageBoxes.container .box-03 { max-width:100%; }

.ttcomponent-homepageBoxes.container .box-01, 
.ttcomponent-homepageBoxes.container .box-02{ overflow:visible;position:relative;}
.ttcomponent-homepageBoxes.container .box-02 {min-height:9rem;}

.ttcomponent-homepageBoxes.container .widget_title {
	text-transform:none;
	font-size:1.3rem;
	padding:0;
	border:0;
	height:inherit;
}
.ttcomponent-homepageBoxes .widget_title + .widget_padding{ margin-top:.65rem;} 
.ttcomponent-homepageBoxes .template-boxTemplate .media-left{ min-width:2.15rem;}
