/* Component basic layout rules */

.slicktemplateslider{
	overflow:hidden;
}
.slickCover .no_content_message{ 
	width:100%;
	min-height:55vh;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	background:none!important;
    border: none!important;
}
.slickCover .no_content_message:before{
	content:'\f695';
	font-family:"Font Awesome 5 Pro";
	font-weight:lighter;
	display:block;
	font-size:3rem;

}
.ttcomponent-slicksliderCover:before {
	content:'';
	position:absolute;
	pointer-events:none;
	bottom:0;
	display:block;
	width:100%;
	height: 100%;
	z-index:1;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0+16,0.5+99 */
	background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 16%, rgba(0,0,0,0.5) 99%, rgba(0,0,0,0.5) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 16%,rgba(0,0,0,0.5) 99%,rgba(0,0,0,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 16%,rgba(0,0,0,0.5) 99%,rgba(0,0,0,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#80000000',GradientType=0 ); /* IE6-9 */
	
}
.ttcomponent-slicksliderCover .no-coverImage .slide-title span {
	padding-bottom:0
}
.ttcomponent-slicksliderCover .no-coverImage .slide-link{
	padding-left: 1.5rem!important;
}


.slickCover .slick-container,
.slickCover .template-slickCover,
.slickCover .slick-track{
	min-width: 100vw;
}

.wrapwidg.slickCover .slide-title{
	font-family: 'Montserrat', sans-serif;
}

.wrapwidg.slickCover #scriptDiv {
	display:none;
}

.wrapwidg.slickCover .slicktemplateslider {
	display:flex;
	flex-wrap:wrap;
}
.wrapwidg.slickCover .slick-dotted.slick-slider {
	margin-bottom: 0;
}
.wrapwidg.slickCover .slick-dotted.slick-slider {
	margin-bottom: 0;
}


.wrapwidg.slickCover {
	background: none;
	border: 0;
}
/* .wrapwidg.slickCover + .wrapwidg{ */
/* 	transform:translateY(-12vh); */
/* 	margin-bottom: -6vh; */
/* } */
.wrapwidg.slickCover .slide .slide-message {
	transform:translateY(-5vh)
}

.wrapwidg.slickCover .slick-arrow {
	opacity: 0;
	-webkit-transition: opacity .3s ease-in-out;
	-o-transition: opacity .3s ease-in-out;
	transition: opacity .3s ease-in-out;
}

.wrapwidg.slickCover .slick-arrow:before {
	background-color: rgba(255, 255, 255, .7);
	padding: .5rem;
}


.wrapwidg.slickCover .slick-arrow.slick-disabled {
	display: none!important;
}

.wrapwidg.slickCover .slick-prev:before {
	content: "\f053";
}

.wrapwidg.slickCover .slick-next:before {
	content: "\f054";
}

.wrapwidg.slickCover .slick-prev:before,
.wrapwidg.slickCover .slick-next:before {
	font-family: "Font Awesome 5 Pro";
	font-size: 18px;
	line-height: 1;
	opacity: .85;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}



.wrapwidg.slickCover .slick-dots li.slick-active button:before {
	content: "";
	opacity: 1;
	border-radius: 1rem;
}

.wrapwidg.slickCover .slick-dots li button:before {
	content: "";
	opacity: 1;
	width:1rem;
	height:1rem;
	border-radius: 1rem;
	border: 2px solid #fff;
	color: transparent!important;
	text-shadow: 0 0 32px rgba(0, 0, 0, 1);
	
}

.wrapwidg.slickCover .slick-dots {
	position: relative;
	transform:translateY(-20vh);
	z-index:100;
	bottom:0;
	display: block;
	width: 100%;
	padding: 0;
	list-style: none;
	text-align: center;
}

.wrapwidg.slickCover .slick-dots li.slick-active button:before {
	content: "";
	opacity: 1;
	border-radius: 1rem;
}


.wrapwidg.slickCover .slick-container:hover .slick-arrow {
	opacity: 1;
}

.slick-arrow {}

.wrapwidg.slickCover .slick-prev,
.wrapwidg.slickCover .slick-next {
	z-index: 10;
}

.wrapwidg.slickCover .slick-prev {
	left: 0;
}

.wrapwidg.slickCover .slick-next {
	right: 5px;
}

.wrapwidg.slickCover {
	padding: 0;
	margin: 0;
	position: relative;
	overflow: hidden;
}

.wrapwidg.slickCover .slide {
	opacity: 0;
	-webkit-transition: opacity .3s ease-in-out;
	-o-transition: opacity .3s ease-in-out;
	transition: opacity .3s ease-in-out;
}

.wrapwidg.slickCover .slick-initialized .slide {
	opacity: 1!important;
}

.wrapwidg.slickCover .slide {
	min-height: 75vh;
}


.wrapwidg.slickCover .widget_padding {
	padding: 0;
	margin: 0;
}

.wrapwidg.slickCover .slide-title {
	-webkit-transform: translateX(-1000px);
	-ms-transform: translateX(-1000px);
	transform: translateX(-1000px);
	transition: -webkit-transform 1.5s;
	-webkit-transition: -webkit-transform 1.5s;
	-o-transition: transform 1.5s;
	transition: transform 1.5s;
	transition: transform 1.5s, -webkit-transform 1.5s;
}

.wrapwidg.slickCover .slick-active .slide-title {
	-webkit-transform: translateX(0);
	-ms-transform: translateX(0);
	transform: translateX(0);
	transition: -webkit-transform .5s;
	-webkit-transition: -webkit-transform .5s;
	-o-transition: transform .5s;
	transition: transform .5s;
	transition: transform .5s, -webkit-transform .5s;
}

.wrapwidg.slickCover .slide-text.subtext-01 {
	-webkit-transform: translateX(-1000px);
	-ms-transform: translateX(-1000px);
	transform: translateX(-1000px);
	transition: -webkit-transform 1s;
	-webkit-transition: -webkit-transform 1s;
	-o-transition: transform 1s;
	transition: transform 1s;
	transition: transform 1s, -webkit-transform 1s;
}

.wrapwidg.slickCover .slick-active .slide-text.subtext-01 {
	-webkit-transform: translateX(0);
	-ms-transform: translateX(0);
	transform: translateX(0);
	transition: -webkit-transform 1s;
	-webkit-transition: -webkit-transform 1s;
	-o-transition: transform 1s;
	transition: transform 1s;
	transition: transform 1s, -webkit-transform 1s;
}

.wrapwidg.slickCover .slide-text.subtext-02 {
	margin-bottom: -.5rem;
	-webkit-transform: translateX(-1000px);
	-ms-transform: translateX(-1000px);
	transform: translateX(-1000px);
	transition: -webkit-transform 2s;
	-webkit-transition: -webkit-transform 2s;
	-o-transition: transform 2s;
	transition: transform 2s;
	transition: transform 2s, -webkit-transform 2s;
}

.wrapwidg.slickCover .slick-active .slide-text.subtext-02 {
	-webkit-transform: translateX(0);
	-ms-transform: translateX(0);
	transform: translateX(0);
	transition: -webkit-transform 1.5s;
	-webkit-transition: -webkit-transform 2s;
	transition: -webkit-transform 2s;
	-o-transition: transform 2s;
	transition: transform 2s;
	transition: transform 2s, -webkit-transform 2s;
}

.slide-link-wrapper {
	margin-top: .5rem;
	overflow: hidden;
	-webkit-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
}

.cover-row-02 + .slide-link-wrapper {
	margin-top: 0!important;
}

.wrapwidg.slickCover .slide-link a {
	display: inline-block;
	padding: .5rem 1.5rem;
	font-size: 16px;
	text-transform: uppercase;
	-webkit-transition: all .1s ease-in-out;
	-o-transition: all .1s ease-in-out;
	transition: all .1s ease-in-out;
	width: auto;
}

.wrapwidg.slickCover .slide-link a::after {
	content: '\f061';
	font-family: "Font Awesome 5 Pro";
	vertical-align: middle;
	display: inline-block;
	font-weight: 300;
	width: 0;
	opacity: 0;
	overflow: hidden;
	-webkit-transform: translateX(-1.5rem);
	-ms-transform: translateX(-1.5rem);
	transform: translateX(-1.5rem);
	-webkit-transition: all .1s ease-in-out;
	-o-transition: all .1s ease-in-out;
	transition: all .1s ease-in-out;
}
.wrapwidg.slickCover .slide-link.video-link a::after {
	content: '\f04b';
	font-family: "Font Awesome 5 Pro";
	vertical-align: middle;
	display: inline-block;
	font-weight: 300;
	width: 0;
	opacity: 0;
	overflow: hidden;
	-webkit-transform: translateX(-1.5rem);
	-ms-transform: translateX(-1.5rem);
	transform: translateX(-1.5rem);
	-webkit-transition: all .1s ease-in-out;
	-o-transition: all .1s ease-in-out;
	transition: all .1s ease-in-out;
}

.wrapwidg.slickCover .slide-link a:hover::after {
	width: auto;
	opacity: 1;
	margin-right: 1rem;
	-webkit-transform: translateX(1.5rem);
	-ms-transform: translateX(1.5rem);
	transform: translateX(1.5rem);
}

.wrapwidg.slickCover .slide-link {
	-webkit-transform: translateY(-100px);
	-ms-transform: translateY(-100px);
	transform: translateY(-100px);
	transition: -webkit-transform .3s;
	-webkit-transition: -webkit-transform .3s;
	-o-transition: transform .3s;
	transition: transform .3s;
	transition: transform .3s, -webkit-transform .3s;
}

.wrapwidg.slickCover .slick-active .cover-row-02 + .slide-link-wrapper .slide-link {
	-webkit-transform: translateX(0);
	-ms-transform: translateX(0);
	transform: translateX(0);
	transition: -webkit-transform 1s;
	-webkit-transition: -webkit-transform 1s;
	-o-transition: transform 1s;
	transition: transform 1s;
	transition: transform 1s, -webkit-transform 1s;
}

.wrapwidg.slickCover .slick-active .cover-row-03 + .slide-link-wrapper .slide-link {
	-webkit-transform: translateX(0);
	-ms-transform: translateX(0);
	transform: translateX(0);
	transition: -webkit-transform 2.5s;
	-webkit-transition: -webkit-transform 2.5s;
	-o-transition: transform 2.5s;
	transition: transform 2.5s;
	transition: transform 2.5s, -webkit-transform 2.5s;
}

.slide-title {
	display: inline-block;
	position: relative;
	overflow: visible;
}

.slide-title-after {
	margin-bottom: .5rem;
}


.slide-title span {
	padding: 1.5rem;
	display: inline-block;
	width:auto;
	vertical-align: middle;
}

.slide-text {
	display: inline-block;
	position: relative;
	overflow: visible;
}

.slide-text span {
	padding: 1rem 1.5rem;
	display: inline-block;
	vertical-align: middle;
}

.slide-text-after.subtext-01:after {
	content: '';
	display: block;
	height: .5rem;
	width: 40%;
	position: absolute;
	left: 0;
	top: 100%;
}

.wrapwidg.slickCover a,
.wrapwidg.slickCover a *,
.wrapwidg.slickCover a:hover,
.wrapwidg.slickCover a:focus,
.wrapwidg.slickCover a:active {
	cursor: pointer;
}

.slide-title h2 {font-weight:300}
.slide-title h2,
.slider-text p {
	padding: 0;
	margin: 0;
}


.slide-message { 
	position:relative; 
	z-index:2
}
.custom-cover {
	position:absolute; 
	width:100%; 
	min-height:100%; 
	max-height:55vh;
	top:0; 
	left:0; 
	z-index:0;
}

.custom-cover.isVideoMode {
	opacity:.2;
	filter: blur(20px) grayscale(100%);
	
}
.wrapwidg.slickCover .video-wrapper  {
	max-width: calc(100vh - 12rem);
    margin: 0 auto;
} 