html {
	font-size:16px; /* Verificare se va tolto dato che già settato nella skin */
}
.cke_editable {
	font-size:1rem;
}

[data-type^="cke-"] {
	margin-top: 1rem;
	margin-bottom:1rem;
}

[data-type] + p {
	margin-top:1rem!important;
}
.content-detail-general-description > p + [data-type] {margin-top:1rem;}

/* IMAGES */
[data-type^='cke'] .ck-tmp-image{
	margin-bottom:1rem
}
[data-type*="quote"] {
	margin: 0 -15px;
}


/* QUOTES */

[data-type="quote-02-alternated"] blockquote {
	position: relative;
	padding-bottom: 10px;
}

[data-type="quote-02-alternated"] blockquote p:after {
	content: '';
	display: block;
	width: 70%;
	height: 10px;
	background: #ccc;
	position: absolute;
	top: calc(100% - 10px);
	right: inherit;
	left: 0;
}

[data-type="quote-02-alternated"]:nth-child(even) blockquote p:after {
	left: inherit;
	right: 0;
	width: 30%;
}

blockquote {
	border: 0;
	margin: 0;
	padding: 0;
}


blockquote{
	display: flex;
}
	
[data-type='cke-quote-00'] blockquote:before {
	content:'\f10d';
	font-family: "Font Awesome 5 Pro";
	background: #f1f1f1;
	display:block;
	padding: 1rem 0 1rem 1rem;
	color:#337ab7;
	font-weight:bold;
}


blockquote p, blockquote div{
	font-size:1rem;
	padding: 1rem;
	background: #f1f1f1;
	margin-bottom:0;
}
blockquote ul {
	padding: 0 1.5rem 1.5rem 2.5rem;
	background: #f1f1f1;
	margin-bottom:0;
}

blockquote p.bg-color-01,
table caption.bg-color-01,
table caption.bg-color-02+tbody>tr>th[scope="row"],
[data-type*="quote-02"] blockquote p.bg-color-02:after {
	background: #337ab7;
	color: #fff;
}

blockquote p.bg-color-02,
table caption.bg-color-02,
table caption.bg-color-04+tbody>tr>th[scope="row"],
[data-type*="quote-02"] blockquote p.bg-color-04:after {
	background: #C62E29;
	color: #fff;
}



[data-type="cke-quote-03"] blockquote { position:relative; margin:1rem 0; display:inline-block; padding-left:1rem;}
[data-type="cke-quote-03"] blockquote > p{padding: 0 0 .5rem 0;  background:#fff; }
[data-type="cke-quote-03"] blockquote > p + small {}


[data-type='cke-quote-03'] blockquote {
	border-left:5px solid #337ab7;
	
}

[data-type='cke-quote-03'] blockquote,
[data-type='cke-quote-03'] blockquote p, 
[data-type='cke-quote-03'] blockquote div {
	background:none;
}
/* List */

[data-type="cke-list"] ul {
	padding-left: 2rem;
}

[data-type="cke-list"] ul li::before {
	content: "•";
	color: #337ab7;
	font-size: 1em;
	display: inline-block;
	width: 1em;
	margin-left: -1em
}
[data-type="cke-list-ribbon"] ul{
        padding-left: 2rem;
}
[data-type*="cke-list-ribbon"] ul li{ margin-bottom:1.3rem;}
[data-type="cke-list-ribbon"] ul li::before {
	content: ""; 
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left .5rem;
    display: block;
    width: 1rem;
    margin-left: -2em;
    height: 1.5rem;
    margin-right: 1rem;
    float: left;
}
[data-type="cke-list-ribbon-ordered"] ul{ counter-reset:ribbonNumber;}
[data-type="cke-list-ribbon-ordered"] ul li::before {
	counter-increment: ribbonNumber;
	content:counter(ribbonNumber); 
	background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left .5rem;
    display: block;
    width: 2rem;
    margin-left: -2em;
    height: 2rem;
    margin-right: 1rem;
    float: left;
    color:#fff;
    text-align:center;
    line-height: 2.7;
    font-size:.85rem;
}


/* tables */

table caption {
	padding: .5rem 1rem;
	font-size: 1.125rem;
	font-weight: 700;
}
.table>thead>tr>th, 
.table>tbody>tr>th, 
.table>tfoot>tr>th, 
.table>thead>tr>td, 
.table>tbody>tr>td, 
.table>tfoot>tr>td {
    padding: .5rem 1rem;
}

 .bootstrap-tabs .tab-content {
 	padding: 2rem 0;
 }
 .external-link:before {
 	content:"\ea08";
 	font-family: linearicons;
 	display:inline-block;
 	vertical-align:middle;
 	margin:0 .3rem 0 .5rem;
 	color:#666;
 }