/* Component basic layout rules */

	.ttcomponent-slickBox .slick-dotted.slick-slider {
		margin-bottom: 30px;
		overflow: visible;
	}
	.ttcomponent-slickBox .contents_wrapper .slick-dots li button {
			line-height: 1;
			display: block;
			width: .75rem;
			height: .75rem;
			padding: 5px;
			cursor: pointer;
			color: transparent;
			border: 0;
			outline: none;
			background: transparent;
			
		}
	.ttcomponent-slickBox .contents_wrapper .slick-dots li.slick-active button:before {
			content:'';
			line-height: 1;
			display: block;
		    width: .75rem;
		    height: .75rem;
			padding: 0;
			cursor: pointer;
			color: transparent;
			border: 0;
			outline: none;
			background: #FFF;
			border-radius:100%;
			border: 2px solid #fff;
		}
	.ttcomponent-slickBox .contents_wrapper .slick-dots li button:before {
		    opacity: 1;
		    width: .75rem;
		    height: .75rem;
		    border-radius: .8rem;
		    border: 2px solid #fff;
		    color: transparent!important;
		    text-shadow: 0 0 32px rgba(0, 0, 0, 1);
		}
		
	.ttcomponent-slickBox [class*='color-0'] .slick-dots li button:before {
			color: #fff;
		}
	.ttcomponent-slickBox [class*='color-0'] .slick-dots li.slick-active button:before {
			color: #fff;
			opacity:1;
		}
	.ttcomponent-slickBox [class*='color-0'] .slick-dots li.slick-active button:before {
			background: #fff;
		}