/* Component basic layout rules */


/* user activity */

#user-activity-container{}
#user-activity-paginator{margin-bottom:20px;text-align:center;width:100%;}

.plugin_userActivity {position:relative; z-index:0;}
#useractivity-widget .layout-buttons-wrapper{ position:relative;}
#useractivity-widget #user-activity-layout { position:absolute; right:0; z-index:10; }
#useractivity-widget #user-activity-layout input[type=radio] + label[for*=-masonry],
#useractivity-widget #user-activity-layout input[type=radio] + label[for*=-vertical] { padding:9px 0 9px 3px; margin:0; width:auto;}
