#useractivity-widget .more-elements{color:#dcdcdc;display:inline-block;font-size:.8em;margin:0 auto 1em;padding:0;text-align:center;text-decoration:none;text-transform:uppercase;}
#useractivity-widget [data-activity] img{max-height:inherit;}
#useractivity-widget [data-activity].element:nth-child(odd) .activity-type h4:before{}
#useractivity-widget div [for=activity-ALL]:before{margin:0;padding:0;width:0;}
#useractivity-widget div[data-view=masonry]{margin-right:0;padding:20px 10px;text-align:right;}
#useractivity-widget div[data-view=masonry] *{float:none;text-align:left;}
#useractivity-widget div[data-view=masonry] .activity-type h4:before{margin:0}
