/* Component basic layout rules */

.ttcomponent-dot-loader {
	min-height: 40px;
	min-width: 40px;
	background-size: cover;
	border-radius: 50%;
	display: inline-block;
}

.ttcomponent-dot-loader {
	display: inline-block;
	position: relative;
	width: 64px;
	height: 64px;
}
.ttcomponent-dot-loader .aDot {
	position: absolute;
	top: 27px;
	width: 11px;
	height: 11px;
	border-radius: 50%;
}

