/* Component basic layout rules */

	.slicknewsroomslider .slick-dotted.slick-slider {
		margin-left: -.5rem;
		margin-right: -.5rem;
	}
	.slicknewsroomslider .slick-list {
		padding-right:.6rem;
		padding-left:.6rem;
	}
	.slicknewsroomslider #homeContentFilter,
	.slicknewsroomslider #postFilter, 
	.slicknewsroomslider #homeContentTypeFilter {
		height:auto;
	}
	.slicknewsroomslider #homeContentTypeFilter ul{
		overflow:hidden;
		padding:.5rem 0;
	}
	.slicknewsroomsliderslider .slick-track {
		  display: -webkit-box;
		  display: -ms-flexbox;
		  display: flex;
		}
	.slicknewsroomsliderslider .slick-track .slick-slide{
		  display: -webkit-box;
		  display: -ms-flexbox;
		  display: flex;
		  height: auto;
		  width:100%;
		}
	.slicknewsroomsliderslider	.slick-track .slick-slide > div:first-child{
		   display: -webkit-box;
		   display: -ms-flexbox;
		   display: flex;
		  	height: calc(100% - 3rem);
		  	width:100%;
		 }
		 
		@-webkit-keyframes xIn {
			0% {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
			100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
		}
		 
		@keyframes xIn {
			0% {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
			100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
		}
		
		.slicknewsroomslider #homeContentTypeFilter ul li a {
			background: none;
			color: #777;
			display: block;
			font-family:montserrat, sans-serif;
			font-weight:300;
			float: left;
			font-size: .85rem;
			height: auto;
			line-height: 1rem;
			margin: 0;
			padding: .5rem 1rem;
			text-decoration: none;
			text-transform:uppercase;
			position:relative;
		}
		.slicknewsroomslider #homeContentTypeFilter ul li a:hover,
		.slicknewsroomslider #homeContentTypeFilter ul li a span.filterOn{
			font-weight:bold;
		}

		.slicknewsroomslider #homeContentFilter, .slicknewsroomslider #postFilter, .slicknewsroomslider #homeContentTypeFilter { width:auto;}
		.slicknewsroomslider #homeContentFilter ul, .slicknewsroomslider #postFilter ul, .slicknewsroomslider #homeContentTypeFilter ul {display:flex; flex-direction:row-reverse; justify-content:flex-start;}
		.slicknewsroomslider #homeContentFilter ul li, .slicknewsroomslider #postFilter ul li, .slicknewsroomslider #homeContentTypeFilter ul li {
		    display: inline-block;
		    float:left;
		    position: relative;
		}

