		.slicknewsroomslider.contents_wrapper { }
		.slicknewsroomslider .slick-track {
			display:-webkit-box;
			display:-ms-flexbox;
			display:flex;
		}
		.slicknewsroomslider .slick-track .slick-slide { height:auto; margin-bottom:2.5rem; position:relative;}	
		
		
		.no-touchevents .slicknewsroomsliderslider .slick-track .slick-slide:hover {
		 	z-index:5;
		}
		.newsroom-message-container, .newsroom-message-container.has-desc {
			background:white!important;
			-webkit-box-flex:1;
				-ms-flex:1;
					flex:1;
			margin:10px;
			-webkit-transition:all .1s ease-in-out;
			-o-transition:all .1s ease-in-out;
			transition:all .1s ease-in-out;
			position:relative;
			z-index:5;
			height:100%;
			margin-bottom:0;
		}
		.newsroom-header {
			-webkit-transition:all .1s ease-in-out;
			-o-transition:all .1s ease-in-out;
			transition:all .1s ease-in-out;
			margin:-15px -15px 15px -15px;
			min-height:220px;
			position:relative;
		} 
		.template-slicknewsroomslider.slick-slide {
			-webkit-transition:all .3s ease-in-out;
			-o-transition:all .3s ease-in-out;
			transition:all .3s ease-in-out;	
			position:relative;
			z-index:1;
		}

		
		.no-touchevents .slicknewsroomsliderslider .slick-track {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			flex-wrap: wrap;
			min-height: 500px;
			height: 100%;
		}
		.template-slicknewsroomslider.slick-slide:not(.slick-active){
			opacity:0;
		}
		
		.template-slickNewsroom .newsroom-message-container { 
			position:relative; 
			z-index:0; 
			transition:transform .4s ease-in-out, box-shadow .3s ease-in-out;
		}
		.template-slickNewsroom:hover .newsroom-message-container {
			transform:scale(1.03);
			box-shadow: 0 5px 35px 0 rgba(0,0,0, .1);
			z-index:1;
		}
		
		.newsroom-type{
			display:inline-block;
			padding:.5rem 0.9375rem;
			color:#fff;
			font-size:0.6875rem;
			position:absolute;
			bottom:0;
			left:15px;
			text-transform:uppercase;
		}
		
		.no-touchevents .newsroom-message-container.has-desc:hover .newsroom-contentype-hover,
		.no-touchevents .newsroom-message-container.has-desc:hover .newsroom-content-description-hover,
		.no-touchevents .newsroom-message-container.has-desc:hover .newsroom-read-more-hover-wrapper  {
			display:block;
			opacity:1;
		}
		.user-tools-newsroom span + span {
			margin-left:1rem;
		}
		.newsroom-contentype-hover .newsroom-type {
			left:-15px;
			top:-30px;
			position:relative;
		}
		
		
		.slicknewsroomslider .slider-custom-arrows > button {
			position:static;
		}
		
		.slicknewsroomslider #more .btn.pager-more{
			display:inline-block;
			vertical-align:middle;
			text-align:center;
			line-height:2rem;
			color:#fff;
			height: 2rem;
			text-transform: uppercase;
			padding: 0 1rem;
		}
		
		.slicknewsroomslider .read-more-wrapper .read-more {
			color:#fff;
			height: 2rem;
			line-height: 1.1;
			text-transform: uppercase;
		}
		.slicknewsroomslider .read-more-wrapper .read-more[disabled] {
		 	background: #ccc;
			color:inherit;
			height: 2rem;
			line-height: 1.1;
			text-transform: uppercase;
		 }
		 .slicknewsroomslider .read-more-squared:after{
		 	content:"\e93c";
		 	font-family:'linearicons';
		 	width:1.5rem;
		 	height:1.5rem;
		 	font-size:.6rem;
		 	line-height:24px;
		 	text-align:center;
		 	display:inline-block;
		 	vertical-align:middle;
		 	color:white;
		 	background:#C21B17;
		 }	 
		 
		 .slicknewsroomslider .slick-slide .read-more-squared:after{ 
		 	margin-left:.5rem;
		 } 
		 
		 .slicknewsroomslider .download-squared:after{
		 	content:"\e8f5";
		 	font-family:'linearicons';
		 	width:1.5rem;
		 	height:1.5rem;
		 	font-size:.6rem;
		 	line-height:24px;
		 	text-align:center;
		 	display:inline-block;
		 	vertical-align:middle;
		 	color:white;
		 	background:#C21B17;
		 	margin-left:.5rem;
		 }
		 .slicknewsroomslider .comments-squared:after{
		 	content:"\e9ec";
		 	font-family:'linearicons';
		 	width:1.5rem;
		 	height:1.5rem;
		 	font-size:.6rem;
		 	line-height:2.3;
		 	text-align:center;
		 	display:inline-block;
		 	vertical-align:middle;
		 	color:white;
		 	background:#E9573D;
		 	/* margin-left:.5rem; */
		 }		 			

		
		.slicknewsroomsliderslider .slick-dotted.slick-slider {
			margin-left:-15px;
			margin-right:-15px;
		}
		.slicknewsroomslider .slickManagement .slick-dots{
			position:relative;
			bottom:0;
		}
		.slick-management-container a:hover { color:#fff;}
			
		.slicknewsroomslider .slick-dots {
			position:relative;
			bottom:0;
			margin-top:2rem;
		}
		.slicknewsroomslider .slick-dots li.slick-active button:before {
			content: "";
			opacity: 1;
			border-radius: 1rem;
		}

		.slicknewsroomslider .slick-dots li button:before {
			content: "";
			opacity: 1;
			width:1rem;
			height:1rem;
			border-radius: 1rem;
			border: 2px solid #6F7072;
			background:transparent;
			color: transparent!important;
			text-shadow: 0 0 32px rgba(0, 0, 0, 1);
			
		}

		.slicknewsroomslider .slick-dots {
			position: relative;
			z-index:100;
			bottom:0;
			display: block;
			width: 100%;
			padding: 0;
			list-style: none;
			text-align: center;
		}

		.slicknewsroomslider .slick-dots li.slick-active button:before {
			content: "";
			opacity: 1;
			border-radius: 1rem;
		}
		
		.slicknewsroomslider .tag { padding: 0 .6rem 0 0; font-size:.8rem;}	
		.slicknewsroomslider .tag + .tag { border-left:1px solid #ccc;  padding: 0 .6rem;}