.bootstrap-dot {
    display: inline-block;
    border-radius: 50%;
    width: 50%;
    background-color: #EEE;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    vertical-align: inherit;
}
.bootstrap-dot.bootstrap-dot-success {
    background-color: #449D44;
}
.bootstrap-dot.bootstrap-dot-warning {
    background-color: #F0AD4E;
}
.bootstrap-dot.bootstrap-dot-danger {
    background-color: #D9534F;
}
.bootstrap-dot.bootstrap-dot-grey {
    background-color: #E0E0E0;
}
.bootstrap-dot.bootstrap-dot-sm {
    height: 10px;
    width: 10px;
}
.bootstrap-dot.bootstrap-dot-lg {
    margin: 0 4px;
    height: 20px;
    width: 20px;
}