:root { font-size:16px;}
body {
	-webkit-font-smoothing: antialiased;
	color: #666;
	font-family: Roboto, arial, sans-serif;
	font-size: 16px;
	font-style: normal;
	font-weight: 300;
}
.cke_editable{font-size:1rem; padding:1.2rem;}
.cke_contents_ltr blockquote{padding-left:20px;padding-right:8px;border-left-width:5px;}
.cke_contents_rtl blockquote{padding-left:8px;padding-right:20px;border-right-width:5px;}
hr{border:0;border-top:1px dashed #ccc; width:100%; clear:both;}
img {cursor: default;max-width: 100%;}
pre{white-space:pre-wrap;word-wrap:break-word;-moz-tab-size:4;-o-tab-size:4;-webkit-tab-size:4;tab-size:4;}
.marker{background-color:Yellow;}
span[lang]{font-style:italic;}
figure{text-align:center;border:solid 1px #ccc;border-radius:2px;background:rgba(0,0,0,0.05);display:inline-block;margin:10px 20px;padding:10px;}
figure > figcaption{text-align:center;display:block;}

/* loader */
.imgLoading {min-height:32px; background-repeat: no-repeat; background-image: url('data:image/gif;base64,');}
